import React from "react";
import { useParams } from "react-router-dom";
import "./toysdetail.css";
import toycardinfoi from "./toydata";
import { useEffect } from "react";

const Toysdetail = () => {
  const { link } = useParams();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <>
      {toycardinfoi
        .filter((product) => product.link === link)
        .map((product, index) => {
          return (
            <div key={index}>
              <div className="toyinfo">
                <div>
                  <img
                    className="toyp"
                    src={product.image}
                    alt={product.title}
                  />
                </div>
                <div className="toyinfo1">
                  <h1 className="">{product.title}</h1>
                  <hr />
                  <h2 className="toyprice">:{product.price}</h2>
                  <hr />
                </div>
              </div>
              <div className="toydesc">
                <h1> :</h1>
                <hr />
                <p>{product.desc}</p>
                <p>{product.desc1}</p>
                <p>{product.desc2}</p>
                <p>{product.desc3}</p>
                <hr />
              </div>
            </div>
          );
        })}
    </>
  );
};
export default Toysdetail;
